import * as Yup from 'yup';
import { useEffect, useState, } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// swal
import swal from 'sweetalert';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Alert, Typography } from '@mui/material';
import axios from '../../../../utils/axios';
import { FormProvider, RHFSelect, RHFTextField, RHFSwitch } from '../../../../components/hook-form';

// format number 
import { fCurrency, } from '../../../../utils/formatNumber';

import useAuth from '../../../../hooks/useAuth';
import { PATH_DASHBOARD } from '../../../../routes/paths';
// ----------------------------------------------------------------------

export default function PurchaseCable() {

  const NewUserSchema = Yup.object().shape({
    iuc: Yup.string().required('IUC Number Required'),
  });
  const pinSchema = Yup.object().shape({
    iuc: Yup.string().required('IUC Number Required'),
    pin: Yup.string().min(4, 'Transaction Pin Must Be 4 Digits').max(4, 'Transaction Pin Must Be 4 Digit').required('Transaction Pin Required'),

  });
  const { enqueueSnackbar } = useSnackbar();
  const [cable, setCable] = useState()
  const [customername, Setcustomer] = useState()
  const [cablename, realCablename] = useState();
  const [cableplan, setcableplan] = useState()
  const [planType, setPlanType] = useState();
  const [amount, SetAmount] = useState();
  const AccessToken = window.localStorage.getItem('accessToken');
  const { setting } = useAuth();
  const pin = setting?.setting?.allow_pin === 1;
  const methods = useForm({
    resolver: yupResolver(pin ? pinSchema : NewUserSchema),
  });
  const navigate = useNavigate();
  const [discount, SetDiscount] = useState();
  const {
    setValue,
    watch,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;


  useEffect(() => {
    axios.get('/api/website/app/cable/lock').then(data => {
      setCable(data.data?.cable);
    });
  }, []);
  const values = watch()
  const bypass = values?.bypass;
  const changeme = async (me) => {
    realCablename(me)
    await axios.get(`/api/cable/plan/${me}/habukhan/system`).then(data => {
      setPlanType(data?.data?.plan);
    }).catch(setPlanType(undefined));

  }
  const cablelist = [
    { name: 'DSTV', code: 2 },
    { name: 'STARTIME', code: 3 },
    { name: 'GOTV', code: 1 },
    { name: 'SHOWMAX', code: 4 }
  ];
  const onSubmit = async (data) => {
    const value = cablelist.filter((item) => item.code === cablename)
    let cableProvider = '';
    if (value[0]) {
      cableProvider = value[0].name
    }
    try {
      const isConfirmed = await swal({
        title: 'Confirmation',
        text: `You are about to make a payment of ${amount} for your cable TV subscription to ${cableProvider}. Your ${cablename === 4 ? 'Phone Number' : 'IUC number'} is ${data.iuc}.`,
        icon: 'warning',
        buttons: ['Cancel', 'Confirm'],
      });
      if (isConfirmed) {
        const resp = await axios.post('/api/cable', {
          cable: cablename,
          iuc: data.iuc,
          cable_plan: cableplan,
          bypass: cablename === 4 ? true : (data?.bypass || false), // Auto-bypass for Showmax
          pin: data.pin,
          token: AccessToken,
        })
        if (resp?.data?.status === 'success') {
          swal({
            title: 'success', text: `${resp?.data?.message}`, icon: 'success', buttons: [
              'Okay',
              'Invoice'
            ],
            dangerMode: true,
          }).then((isConfirm) => {
            if (isConfirm) {
              navigate(`${PATH_DASHBOARD.general.invioce}/${resp?.data?.transid}/cable`, { replace: true });
            }
            window.location.reload();
          });
        } else if (resp?.data?.status === 'fail') {
          swal('Opps', `${resp?.data?.message}`, 'error');
        } else {
          swal('Info', `${resp?.data?.message}`, 'info');
        }
      }
    } catch (error) {
      if (error?.data?.message !== undefined) {
        swal('Opps', `${error?.data?.message}`, 'error');
      } else if (error?.message !== undefined) {
        swal('Opps', `${error?.message}`, 'error');
      } else {
        swal('Opps', 'Something went wrong', 'error');
      }
    }
  };


  const Calculate = async (data) => {
    setcableplan(data.target.value)
    axios.get(`/api/cable/charges/${data.target.value}/admin`).then(data => {
      SetDiscount(`₦${fCurrency(data?.data?.charges)}`);
      SetAmount(`₦${fCurrency(data?.data?.amount)}`)
    }).catch((error) => {
      SetDiscount(undefined);
      SetAmount(undefined)
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    })
  }
  const verify = async (data) => {
    try {
      const resp = await axios.get(`/api/cable/cable-validation?iuc=${data.iuc}&cable=${cablename}`)
      setValue('bypass', true)
      Setcustomer(resp?.data?.name)

    } catch (error) {
      Setcustomer(undefined)
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(bypass ? onSubmit : verify)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 1,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              <RHFSelect name="cable" label="Cable Name" onChange={(e) => changeme(e.target.value)} value={cablename}>
                <option value="" />
                {cable !== undefined && cablelist?.map((option) => (
                  <option key={option?.code} value={option?.code} disabled={option?.code === 1 && cable?.gotv == 0 || option?.code === 2 && cable?.dstv == 0 || option?.code === 3 && cable?.startime == 0 || option?.code === 4 && cable?.showmax == 0}>
                    {option?.name}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect name="plan" label="Cable Plan" value={cableplan} onChange={Calculate}>
                <option value="" />
                {planType?.map((option) => (
                  <option key={option?.plan_id} value={option?.plan_id}>
                    {`${option?.plan_name} - ₦${fCurrency(option?.plan_price)}`}
                  </option>
                ))}
              </RHFSelect>
              {/* Dynamic label: Phone Number for Showmax, IUC NUMBER for others */}
              <RHFTextField name="iuc" label={cablename === 4 ? "Phone Number" : "IUC NUMBER"} />

              {pin && <RHFTextField name="pin" label="Transaction Pin" type="password" autoComplete="new-password" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />}
              {amount !== undefined && <RHFTextField name="amount" label="Amount" value={amount} disabled />}
              {discount !== undefined && <RHFTextField name="charges" label="Charges" value={discount} disabled />}
              {customername !== undefined && <RHFTextField name="customername" label="customer name" value={customername} disabled />}
              <RHFSwitch
                name='bypass'
                labelPlacement="start"
                def={false}
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      Bypass IUC Number
                    </Typography>
                  </>
                }
                sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
              />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                {bypass || cablename === 4 ? 'Purchase' : 'Verify IUC Number'}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
